<template>
  <div id="box" @mouseover="handleBoxOver" @mouseout="handleBoxOut">
    <div class="list">
      <ul ref="myUl">
        <li ref="myLi"><img src="./assets/lesson7/01.jpg"></li>
        <li><img src="./assets/lesson7/02.jpg"></li>
        <li><img src="./assets/lesson7/03.jpg"></li>
        <li><img src="./assets/lesson7/04.jpg"></li>
        <li><img src="./assets/lesson7/05.jpg"></li>
      </ul>
    </div>
    <ul class="count">
      <li v-for="(item, index) in btns" 
      :key="index" 
      @mouseover="handleBtnOver(index)"
      :class="{current:item.isCurrent}"
      >
        {{ index + 1 }}
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  data(){
    return{
      index:0,
      bOrder:true,
      playTimer:null,
      timer:null,
      btns:[
        {isCurrent:true},
        {isCurrent:false},
        {isCurrent:false},
        {isCurrent:false},
        {isCurrent:false}
      ]
    }
  },
  methods:{
    handleBoxOver(){
      clearInterval(this.playTimer);
    },
    handleBoxOut(){
      var _this=this;
      this.playTimer=setInterval(() => {
        _this.next();
      }, 3000);
    },
    handleBtnOver(number){
      this.index=number;
      this.cutover();
    },
    cutover(){
      for(let i =0;i<this.btns.length;i++) this.btns[i].isCurrent=false;
      this.btns[this.index].isCurrent=true;;
      this.startMove(-(this.index * this.$refs.myLi.offsetHeight));

    },
    next(){
      this.bOrder ? this.index++ : this.index--;
      this.index <=0 && (this.index =0, this.bOrder = true);
      this.index >= this.btns.length - 1 && (this.index = this.btns.length -1, this.bOrder = false);
      this.cutover()
    },
    startMove(iTarget){
      var _this=this;
      clearInterval(this.timer);
      this.timer=setInterval(() => {
        _this.doMove(iTarget);
      }, 20);
    },
    doMove(iTarget){
      var iSpeed=(iTarget-this.$refs.myUl.offsetTop) / 10;
      iSpeed=iSpeed > 0 ? Math.ceil(iSpeed) : Math.floor(iSpeed);
      this.$refs.myUl.offsetTop == iTarget ? clearInterval(this.timer) : this.$refs.myUl.style.top = this.$refs.myUl.offsetTop + iSpeed + 'px';
    }
  },
  mounted(){
    this.playTimer=setInterval(() => {
      this.next();
    }, 3000);
  }
}
</script>
<style>
body, div,ul, li{
  margin: 0;
  padding: 0;
}
ul{
  list-style-type: none;
}
body{
  background: #000;
  text-align: center;
  font: 12px/20px arial;
}
#box{
  position: relative;
  width: 492px;
  height: 172px;
  background: #fff;
  border-radius: 5px;
  border: 8px solid #fff;
  margin: 10px auto;
  cursor: pointer;
}
#box .list{
  position: relative;
  width: 490px;
  height: 170px;
  overflow: hidden;
}
#box .list ul{
  position: absolute;
  top:0;
  left: 0;
}
#box .list li{
  width: 490px;
  height: 170px;
  overflow: hidden;
}
#box .count{
  position: absolute;
  right: 0;
  bottom: 5px;
}
#box .count li{
  color: #fff;
  float: left;
  width: 20px;
  height: 20px;
  cursor: pointer;
  margin-right: 5px;
  overflow: hidden;
  background: #f90;
  opacity: 0.7;
  filter: alpha(opacity=70);
  border-radius: 20px;
}
#box .count li.current{
  color: #fff;
  opacity: 1;
  filter: alpha(opacity=100);
  font-weight: 700;
  background: #f60;
}
</style>